import * as THREE from 'three';
import { MeshLine, MeshLineMaterial } from './meshline';
import { geoInterpolate } from 'd3-geo';

const r = 230;
const GLOBE_RADIUS = 221;
export const CURVE_SEGMENTS = 32;

const CURVE_MIN_ALTITUDE = 20;
const CURVE_MAX_ALTITUDE = 200;
const DEGREE_TO_RADIAN = Math.PI / 180;


function clamp(num, min, max) {
  return num <= min ? min : (num >= max ? max : num);
}


function coordinateToPosition(lat, lng, radius) {
  const phi = (90 - lat) * DEGREE_TO_RADIAN;
  const theta = (lng + 180) * DEGREE_TO_RADIAN;

  return new THREE.Vector3(-radius * Math.sin(phi) * Math.cos(theta),
    radius * Math.cos(phi),
    radius * Math.sin(phi) * Math.sin(theta)
  );
}

function getSplineFromCoords(src, target) {

  const startLat = src.lat
  const startLng = src.lon
  const endLat = target.lat
  const endLng = target.lon

  // spline vertices
  const start = coordinateToPosition(startLat, startLng, GLOBE_RADIUS);
  const end = coordinateToPosition(endLat, endLng, GLOBE_RADIUS);
  const altitude = clamp(start.distanceTo(end) * .15, CURVE_MIN_ALTITUDE, CURVE_MAX_ALTITUDE);
  const interpolate = geoInterpolate([startLng, startLat], [endLng, endLat]);
  const midCoord1 = interpolate(0.25);
  const midCoord2 = interpolate(0.75);
  const mid1 = coordinateToPosition(midCoord1[1], midCoord1[0], GLOBE_RADIUS + altitude);
  const mid2 = coordinateToPosition(midCoord2[1], midCoord2[0], GLOBE_RADIUS + altitude);

  return {
    start,
    end,
    spline: new THREE.CubicBezierCurve3(start, mid1, mid2, end)
  };
}

// var material = new THREE.MeshBasicMaterial({ color: 0xf37b1d, transparent: true, opacity: 0.7 });

function createVector(latRad, lonRad) {
  return new THREE.Vector3(Math.cos(latRad) * Math.cos(lonRad) * r, Math.sin(latRad) * r, Math.cos(latRad) * Math.sin(lonRad) * r);
}

function rayLine(vertices) {
  // const vertices = spline.getPoints(160);

  const material = new MeshLineMaterial({
    color: new THREE.Color(0xdd514c),
    lineWidth: 6,
    opacity: 1,
    transparent: true,
    side: THREE.FrontSide
  });

  const geometry = new THREE.Geometry();
  for (let i = 0; i < CURVE_SEGMENTS - 1; i++) {
    geometry.vertices.push(new THREE.Vector3())
    // geometry.vertices.push(null)
  }

  const line = new MeshLine();
  line.setGeometry(geometry)

  animate(vertices, line)

  const mesh = new THREE.Mesh(line.geometry, material);
  mesh.renderOrder = 2;
  mesh.material.depthTest = false;
  return mesh;
}

let end = 0;

function animate(vertices, line) {

  let position = vertices[end];

  if (end > vertices.length - 1) {
    position = vertices[vertices.length - 1];
    // position = new THREE.Vector3()
  }

  if (end > vertices.length + CURVE_SEGMENTS - 1) {
    // end = -1;
    position = null;
    // position = vertices[end];
  }

  if (end > vertices.length + 2 * CURVE_SEGMENTS - 2) {
    // console.log('.......')
    end = -1;
    // position = null;
    // position = vertices[end];
  }

  end += 1;

  line.advance(position);

  setTimeout(function() {
    animate(vertices, line)
  }, 32)
  // requestAnimationFrame(function() {
  //   animate(vertices, line)
  // })
}

function createLine(vertices) {

  const material = new MeshLineMaterial({
    color: new THREE.Color(0xffffff),
    lineWidth: 6,
    opacity: 0.2,
    transparent: true,
    side: THREE.DoubleSide
  });

  var geometry = new THREE.Geometry();
  geometry.vertices = vertices;
  geometry.colors.push(new THREE.Color(0xffffff), new THREE.Color(0x8d8e81))
  var line = new MeshLine();
  line.setGeometry(geometry, null, 0.2)


  return new THREE.Mesh(line.geometry, material);
}

let group = new THREE.Mesh(new THREE.Geometry());
export default (attacks = [], earth) => {
  earth.remove(group)

  // group = new THREE.Group();
  group = new THREE.Mesh(new THREE.Geometry());

  attacks.map(attack => {
    const { spline } = getSplineFromCoords(attack.attack_ip.location, attack.attack_prefer.location);
    const vertices = spline.getPoints(CURVE_SEGMENTS - 1);

    group.add(createLine(vertices));
    group.add(rayLine(spline.getPoints(CURVE_SEGMENTS - 1)));
  })
  earth.add(group)

  return group
}
