<template>
  <div class="ball-wrap">
    <div id="ball">
    <div class="topglow"></div>
    </div>
  </div>
</template>

<style>
.ball-wrap {
  position: relative;
  width: 100%;
  height: 80%;
}

.topglow {
  position: absolute;
  top: -13%;
  /* top: calc(50% - 50px); */
  /* left: 50%; */
  width: 100%;
  height: 100%;
  opacity: 0.9;
  /* transform: translateY(-50%) translateX(-50%); */
  background: url(./top2.png) center center no-repeat;
  background-size: 71%;
}

#ball {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  height: 100%;
}

</style>

<script>
import * as THREE from 'three';
import jpg from './earth.jpg'
import createMarker from './marker'
import createLines from './lines0'

export default {
  name: 'Global',
  props: ['attacks'],
  beforeUpdate: function() {
    console.log(this.attacks)
  },
  mounted: function() {
    const el = document.getElementById('ball')
    const scene = new THREE.Scene();
    // const group = new THREE.Group();
    const group = new THREE.Mesh(new THREE.Geometry());
    const style = el.getBoundingClientRect();
    // const height = style.height;
    const renderer = new THREE.WebGLRenderer({ alpha: true });
    const camera = new THREE.PerspectiveCamera( 45, style.height / style.height, 0.1, 1500 );
    camera.position.set( 0, -30, 500 );

    camera.add(new THREE.AmbientLight( 0x3355443 ));
    camera.add(new THREE.SpotLight( 0xffffff ));
    // camera.add(new THREE.SpotLight( 0xffffff ));

    scene.add(camera)
    scene.add( group );

    this.$watch('attacks', ()=> {
      createMarker(this.attacks, group)
      // createLines(this.attacks, group )
    })


    createMarker(this.attacks, group)
    createLines(this.attacks, group)

    renderer.setSize( style.height, style.height);
    renderer.setPixelRatio(window.devicePixelRatio);

    const DEGREE_TO_RADIAN = Math.PI / 180;
    function coordinateToPosition(lat, lng, radius) {
      const phi = (90 - lat) * DEGREE_TO_RADIAN;
      const theta = (lng + 180) * DEGREE_TO_RADIAN;

      return new THREE.Vector3(
        - radius * Math.sin(phi) * Math.cos(theta),
        radius * Math.cos(phi),
        radius * Math.sin(phi) * Math.sin(theta)
      );
    }

    el.appendChild( renderer.domElement );

    let loader = new THREE.TextureLoader();

    loader.load(jpg, ( texture ) => {
      var geometry = new THREE.SphereGeometry( 220, 50, 50 );
      // var material = new THREE.MeshBasicMaterial( { map: texture  , overdraw: 0.5} );
      var material = new THREE.MeshPhongMaterial({
        map: texture,
        overdraw: 0.5,
        // shininess: 20,
        transparent: true,
        // specular: new THREE.Color('#909090'),
      });
      // material.specular = new THREE.Color('#909090');
      var mesh = new THREE.Mesh( geometry, material );

      group.add( mesh );
      group.position.z = -300;

      animate();
    });

    function animate() {
      requestAnimationFrame(animate);
      render();
    }

    function render() {

      // camera.position.y += 2;
      // camera.position.x += 2;
      // camera.position.x += 2;
      // group.rotation.y = -2;
      group.rotation.y -= 0.005;
      // group.rotation.y = -3;
      // group.rotation.x -= 0.01;
      // camera.position.y += - camera.position.y * 0.05;
      // camera.lookAt( scene.position );
      // console.log(camera.position, scene.position)
      renderer.render( scene, camera );
    }
  }
}
</script>
