<template>
  <div id="global" ref="el"></div>
</template>

<style>
#global {
  width: 100%;
  height: 80%;
}
</style>

<script>
import * as THREE from 'three';
import jpg from './earth.jpg'
import createMarker from './marker'
import createLines from './lines';
import hilbert3D from './h3d'

export default {
  name: 'Global',
  props: ['attacks'],
  beforeUpdate: function() {
    console.log(this.attacks)
  },
  mounted: function() {
    const scene = new THREE.Scene();
    const group = new THREE.Mesh();
    const style = this.$el.getBoundingClientRect();
    const renderer = new THREE.WebGLRenderer({ alpha: true });

			document.getElementById('global').appendChild(renderer.domElement);

			renderer.setSize(style.width, style.height);
			renderer.setClearColor(0x000000, 1);

    const camera = new THREE.PerspectiveCamera( 45, style.width / style.height, 0.1, 1500 );
    camera.position.set( 0, -30, 200 );
    // scene.add(camera)

			var positions = [];
			var colors = [];

			var points = hilbert3D(new THREE.Vector3(0, 0, 0), 20.0, 1, 0, 1, 2, 3, 4, 5, 6, 7);

			var spline = new THREE.CatmullRomCurve3(points);
			var divisions = Math.round(12 * points.length);
			var color = new THREE.Color();

			for (var i = 0, l = divisions; i < l; i++) {

				var point = spline.getPoint(i / l);
				positions.push(point.x, point.y, point.z);

				color.setHSL(i / l, 1.0, 0.5);
				// colors.push(color.r, color.g, color.b);
        colors.push(255,255,255)

			}
			// THREE.Line2 ( LineGeometry, LineMaterial )

			var geometry = new THREE.LineGeometry();
			geometry.setPositions(positions);
			geometry.setColors(colors);

			var matLine = new THREE.LineMaterial({

				color: 0xffffff,
				linewidth: 8, // in pixels
				vertexColors: THREE.VertexColors,
				//resolution:  // to be set by renderer, eventually
				dashed: false,
        transparent: true,
        // opacity: .4

			});

			var line = new THREE.Line2(geometry, matLine);
			line.computeLineDistances();
			line.scale.set(1, 1, 1);
			scene.add(line);

			// renderer.setViewport(0, 0, style.width, style.height);

			// renderer will set this eventually
			matLine.resolution.set(style.width, style.height)// resolution of the viewport
			renderer.render(scene, camera);
  }
}
</script>
